<template>
  <div class="textColumn">
    <div class="myH3">{{ $t("videos") }}</div>

    <div v-for="item in videoJson" :key="item">
      <div
        class="h3"
        style="
          display: flex;
          text-align: center;
          color: var(--bs-dark);
          align-items: center;
        "
      >
        <CIcon :icon="cilFeaturedPlaylist" class="playlistIcon" />
        {{ $t(item.channel) }}
      </div>
      <div
        class="myH5"
        v-for="videoInfo in item.video"
        :key="videoInfo.id"
        style="margin: 20px auto 50px auto"
      >
        <div class="setTitle">
          {{ videoInfo.title }}
        </div>
        <div class="ratio ratio-16x9 videoStyle">
          <iframe
            :src="videoInfo.video"
            class="embed-responsive-item"
            loading="lazy"
            allow="autoplay;"
            allowfullscreen
          ></iframe>
          <div class="overlay" @click="previewImgClick(videoInfo, $event)">
            <CIcon :icon="cilMediaPlay" class="setIcon" />
            <img class="thumbnail" :src="videoInfo.thumbnail" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.size {
  width: 560px;
  height: 315px;
}
.overlay {
  z-index: 120;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: center;
}

/*去除低解析度縮圖上下黑邊*/
.thumbnail {
  position: absolute;
  z-index: 50;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  filter: brightness(0.4);
  pointer-events: none;
}

.playlistIcon {
  width: 2rem;
  margin-right: 10px;
}

.setIcon {
  border-radius: 99%;
  border: #fff thick solid;
  width: 17%;
  padding: 1%;
  padding-left: 1.4%;
  pointer-events: none;
  z-index: 120;
  color: #fff;
}

.setTitle {
  text-align: justify;
}

@media (max-width: 725px) {
  .setTitle {
    font-size: 1rem;
    line-height: 1.4rem;
  }
}
</style>

<script>
import { reactive } from "vue";
import json from "@/assets/press/videoList.json";
import { CIcon } from "@coreui/icons-vue";
import { cilList, cilMediaPlay, cilFeaturedPlaylist } from "@coreui/icons";

export default {
  data() {
    return {
      thumbnailTypes: [
        "maxresdefault",
        "mqdefault",
        "sddefault",
        "hqdefault",
        "default",
      ],
    };
  },
  methods: {
    loadImage(url) {
      return new Promise((resolve, reject) => {
        const image = new Image();
        image.onload = () => {
          if (image.naturalWidth <= 120 && image.naturalHeight <= 90) {
            reject(url); // 圖片太小，視為加載失敗
          } else {
            resolve(url);
          }
        };
        image.onerror = () => reject(url);
        image.src = url;
      });
    },
    async loadThumbnail() {
      for (let j = 0; j < this.videoJson.length; j++) {
        let videos = this.videoJson[j].video;
        for (let video of videos) {
          for (let type of this.thumbnailTypes) {
            const url = `https://img.youtube.com/vi/${video.id}/${type}.jpg`;
            try {
              const loadedUrl = await this.loadImage(url);
              video.thumbnail = loadedUrl;
              break;
            } catch (error) {
              console.debug(error);
              console.log(`Load ${type} image failed, try lower the size.`);
            }
          }
        }
      }
    },
  },
  created() {
    this.loadThumbnail();
  },
  setup() {
    const videoJson = reactive(json);

    const previewImgClick = (videoInfo, e) => {
      var clickedElement = e.target;
      $(clickedElement).hide();
      // show video
      console.log("clickedElement", clickedElement);
      videoInfo.video = `https://www.youtube.com/embed/${videoInfo.id}?autoplay=1`;
    };

    return {
      videoJson,
      previewImgClick,
      CIcon,
      cilList,
      cilMediaPlay,
      cilFeaturedPlaylist,
    };
  },
};
</script>
